import * as d3 from 'd3'
import { xAxisYear } from '../general'

export function trend2({
  // Default options in here
  selector = 'body',
  elid = 'trend2-chart',
  width = 300,
  height = 200,
  margin = {left: 35, right: 0, top: 20, bottom: 5},
  expand = false,
  axisLabelFontSize = 10,
  axisLeft = 'tick',
  axisBottom = 'tick',
  axisRight = '',
  axisTop = '',
  axisLeftLabel = '',
  duration = 1000,
  data = [],
  means = []
} = {}) {

  const updateChart = makeChart(data, means, selector, elid, width, height, margin, expand, axisLeft, axisRight, axisTop, axisBottom, axisLeftLabel, axisLabelFontSize)

  return {
    updateChart: updateChart
  }
}

function maxYear(data) {
  return Math.max(...data.map(d => d.year))
}
function minYear(data) {
  return Math.min(...data.map(d => d.year))
}
function maxY(data, means) {
  const dMax = Math.max(...data.map(d => d.upper ? d.upper : d.value))
  const mMax = Math.max(...means.map(d => d.mean + d.sd))
  return Math.max(dMax, mMax)
}
function minY(data, means) {
  const dMin = Math.min(...data.map(d => d.lower ? d.lower : d.value))
  const mMin = Math.min(...means.map(d => d.mean - d.sd))
  return Math.max(dMin, mMin)
}

function makeChart(data, means, selector, elid, width, height, margin, expand, axisLeft, axisRight, axisTop, axisBottom, axisLeftLabel, axisLabelFontSize) {

  const svgWidth = width + margin.left + margin.right
  const svgHeight = height + margin.top + margin.bottom

  // Append the chart svg
  const svgTrend = d3.select(`${selector}`)
    .append('svg')
    .attr('id', elid)

  // Size the chart svg
  if (expand) {
    svgTrend.attr("viewBox", `0 0 ${svgWidth} ${svgHeight}`)
  } else {
    svgTrend.attr("width", svgWidth)
    svgTrend.attr("height", svgHeight)
  }

  // Axis labels
  if (axisLeftLabel) {
    const axisLeftLabelTrans = `translate(${axisLabelFontSize},${margin.top + height/2}) rotate(270)`
    svgTrend.append("text")
      .attr("transform", axisLeftLabelTrans)
      .style("text-anchor", "middle")
      .style('font-size', axisLabelFontSize)
      .text(axisLeftLabel) 
  }

  // Data
  const dataWork = data.sort((a, b) => (a.year > b.year) ? 1 : -1)
  const yearMin = minYear(dataWork)
  const yearMax = maxYear(dataWork)
  const yMin = minY(dataWork, means)
  const yMax = maxY(dataWork, means)

  // Value scales
  const xScale = d3.scaleLinear().domain([yearMin, yearMax]).range([0, width])
  const yScale = d3.scaleLinear().domain([yMin, yMax]).range([height, 0])

  // Axes
  let tAxis
  if (axisTop === 'on') {
    tAxis = d3.axisTop()
      .scale(xScale) // Actual scale doesn't matter, but needs one
      .tickValues([])
      .tickSizeOuter(0)
  }
  let bAxis
  if (axisBottom === 'on' || axisBottom === 'tick') {
    bAxis = xAxisYear(width, axisBottom === 'tick', yearMin, yearMax, false)
  }
  let lAxis
  if (axisLeft === 'on' || axisLeft === 'tick') {
    lAxis = d3.axisLeft()
      .scale(yScale)
      .ticks(5)
  }
  let rAxis
  if (axisRight === 'on') {
    rAxis =  d3.axisRight()
      .scale(yScale)
      .tickValues([])
      .tickSizeOuter(0)
  }

  // Create axes and position within SVG
  const axisLeftTrans = `translate(${margin.left},${margin.top})`
  const axisRightTrans = `translate(${margin.left + width}, ${margin.top})`
  const axisTopTrans = `translate(${margin.left},${margin.top})`
  const axisBottomTrans = `translate(${margin.left},${margin.top + height})`
  
  // Create axes and position within SVG
  if (lAxis) {
    svgTrend.append("g")
      .attr("transform", axisLeftTrans)
      .call(lAxis)
  }
  if (bAxis) {
    svgTrend.append("g")
      .attr("transform", axisBottomTrans)
      .call(bAxis)
  }
  if (tAxis) {
    svgTrend.append("g")
      .attr("transform", axisTopTrans)
      .call(tAxis)
  }
  if (rAxis) {
    svgTrend.append("g")
      .attr("transform", axisRightTrans)
      .call(rAxis)
  }

  // Create the API function
  const updateChart = makeUpdateChart(svgTrend, width, height)

  return updateChart
}

function makeUpdateChart(
  svgTrend,
  width,
  height,
  tAxis,
  bAxis,
  lAxis,
  rAxis
) {
  const fn = (data, means) => {
    // Data
    const dataWork = data.sort((a, b) => (a.year > b.year) ? 1 : -1)
    const yearMin = minYear(dataWork)
    const yearMax = maxYear(dataWork)
    const yMin = minY(dataWork, means)
    const yMax = maxY(dataWork, means)

    // Value scales
    const xScale = d3.scaleLinear().domain([yearMin, yearMax]).range([0, width])
    const yScale = d3.scaleLinear().domain([yMin, yMax]).range([height, 0])
  }

  return fn
}